.Result {
  display: block;
  width: 85%;
  max-width: 350px;
  margin: 20px auto;

}

.Arrow {  
  display: block;
  width: 50%;
  margin: 20px auto;
}

.canvas {
border: 1px solid black;
background: white;
width: 80%;
display: block;
margin: 0 auto;
}

.canvasForSave {
border: 1px solid black;
background: white;
width: 70%;
display: block;
margin: 0 auto;
}

.canvasFormat {
position: absolute;
border: 1px solid #45b3e6;
width: 30%;
}

.canvasFormat2 {
position: relative;
width: 30%;
display: block;
margin: 0 auto;
}

.scale {
  display: block;
  width: 50%;
  margin: 20px auto;
  animation: scale 0.5s infinite alternate;
}
 
@keyframes scale { 

    0% { 
        transform: translateX(-50px);
    } 
    
    100% { 
        transform: translateX(50px);
    } 
}
